<template lang="html">
  <div class="demo-modal-tip">
    <modal-tip :show="show" msg="但还是卡简单顶级辣椒的"></modal-tip>
    <button type="button" class="demo-modal-tip-button" @click="showTip">Tip</button>
  </div>
</template>

<script>
import ModalTip from 'components/modal/tip.vue'

export default {
  name: 'demo-modal-tip',
  components: {
    ModalTip
  },
  data () {
    return {
      show: false
    }
  },
  methods: {
    showTip () {
      this.show = true
      setTimeout(() => {
        this.show = false
      }, 3000)
    }
  }
}
</script>

<style lang="pcss">
@namespace demo {
  @b modal-tip {
    @e button {
      font-size: 28px;
      color: #fff;
      width: 100%;
      padding: 20px 0;
      margin-bottom: 1px;
      background-color: #65b1db;
    }
  }
}
</style>



